@import './utils/variables';
body {
  background-color: $primary-background-color;
  color: $primary-color;
}
li {
  list-style: none;
}
ul {
  margin-top: 0px;
  margin-bottom: 0px;
  padding-left: 0px;
}
h1 {
  margin-top: 0px;
  margin-bottom: 0px;
}
button {
  font-style: normal;
  font-family: inherit;

  cursor: pointer;
}
.container {
  display: flex;
  flex-direction: column;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}
