.hero-phrase {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
}
.search-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}
.input-container {
  padding: 5px 10px;
  margin-right: 20px;
  border-radius: 10px;
  border: 2px solid yellow;
}
.button-submit,
.button-load {
  background-color: $primary-color;
  border-radius: 10px;
  color: $accent-color;
}
.button-load {
  margin-left: auto;
  margin-right: auto;
}
.is-hidden {
  opacity: 0;
  pointer-events: none;
  transform: scale(0.75);
}
